.glass {
  background: rgba(255, 255, 255, 0.55);
  border-radius: 16px;
  box-shadow: 0 4px 30px #4747470b;
  backdrop-filter: blur(7.1px);
  height: 80%;
  border: 1px solid rgba(255, 255, 255, 0.3);
  @apply border-4 border-gray-50 shrink-0 w-[30%] rounded-3xl py-20 px-7 min-w-max;
}

.profile-img {
  @apply border-4 border-gray-100 w-[135px] rounded-full shadow-lg cursor-pointer;
  @apply hover:border-gray-200;
}
.textbox {
  width: 100%;
}
.textbox-input {
  @apply border-0 px-5 py-3 rounded-xl w-3/4 shadow-sm text-lg;
  @apply focus:outline-none;
}

.btn {
  @apply border bg-indigo-500 w-3/4 py-3 rounded-lg text-gray-50 text-xl shadow-sm text-center;
}
.btn:hover {
  background-color: #ff6a6a;
}
